<template>
	<view :class="{body:models=='1'||models=='', bodyNight:models=='0'}">
		<!-- 弹窗 -->
		<view class="model" v-if="showAudition">
			<view class="models">
				<img src="../../static/index/modelBg.png">
				<view class="closeModel" @click="closeAudition">×</view>
				<view class="contents">
					<view class="modelTitle">请输入邀请码</view>
					<input type="text" v-model="auditionCode"/>
					<view class="buttons">
						<button class="sure" @click="sendAuditionCode">确定</button>
						<button class="contact" open-type="contact">联系客服</button>
					</view>
				</view>
			</view>
		</view>
		<view class="times">
			<view :class="{checked:current==index}" @click="current=index" v-for="(item,index) in data" :key="index">{{item.title}}</view>
		</view>
		<view class="content">
			<view class="tabarList" >
				<view :class="{checkTabar:check==index}" v-for="(item,index) in data[current].next" :key="index" @click="changeTab(item,index)">
					{{item.title}}
				</view>
			</view>
			<view class="questionList">
				<view class="question" v-for="(item,index) in dataList.data" :key="index" @click="toDetiel(item)">
					<view class="title">{{item.title}}</view>
					<view v-if="item.is_complete == 0" class="unfinished">未完成</view>
					<view v-else-if="item.is_complete == null" class="notDone">未做</view>
					<view v-else class="finished">查看报告</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { simulationBrush, simulationBrushMenu, simulationBrushList, simulationBrushListCollection, simulationBrushListWrong , simulationBrushWrong, simulationBrushCollection, question, invitation } from '@/common/http.api.js';
	export default{
		data() {
			return {
				showAudition:false, //弹窗显示状态
				auditionCode:'', //邀请码
				current:0, //当前选中年份
				check:0, //当前选中题集
				time:'', //当前年份
				models:'1', //白天模式
				token:'',
				wrong:'',
				id:'',
				data:{},
				dataList:{},
				enter:'',
				currents:'',
				pages:1,
				allPages:0
			}
		},
		// 滑动到页面底部加载下一页
		onReachBottom(){
			if(this.enter==0){
				// 首页
				// 判断是否有数据
				if(this.pages>=this.allPages){
					this.$u.toast("暂无更多")
				}else{
					this.pages++
					console.log(this.pages)
					simulationBrushList({token:this.token, id:this.id}).then(res=>{
						console.log(res)
						this.dataList.data.push(...res.data.data.data)
					}).catch(err=>{
						console.log(err)
					})
				}
			}else{
				console.log("复习入口")
				// 复习
				if(this.currents==0){
					//错题
					// 判断是否有数据
					if(this.pages>=this.allPages){
						this.$u.toast("暂无更多")
					}else{
						this.pages++
						console.log(this.pages)
						simulationBrushListWrong({token:this.token, id:this.id}).then(res=>{
							console.log(res)
							this.dataList.data.push(...res.data.data.data)
						}).catch(err=>{
							console.log(err)
						})
					}
				}else{
					 //收藏
					// 判断是否有数据
					if(this.pages>=this.allPages){
						this.$u.toast("暂无更多")
					}else{
						this.pages++
						console.log(this.pages)
						simulationBrushListCollection({token:this.token, id:this.id}).then(res=>{
							console.log(res)
							this.dataList.data.push(...res.data.data.data)
						}).catch(err=>{
							console.log(err)
						})
					}
				}
			}
			
		},
		onLoad(option) {
			 this.time = new Date().getFullYear()
			 console.log(this.time)
			 this.models = store.state.model
			 this.token = store.state.vuex_token
			 this.enter = option.enter
			 this.currents = option.currents
			 if(this.models=='0'){
			 	console.log('进入夜间模式')
			 	uni.setNavigationBarColor({
			 	    frontColor: '#ffffff',
			 	    backgroundColor: '#303030',
			 	    animation: {
			 	        duration: 0,
			 	        timingFunc: 'easeIn'
			 	    }
			 	})
			 }else{
			 	console.log('进入白天模式')
			 	uni.setNavigationBarColor({
			 	    frontColor: '#ffffff',
			 	    backgroundColor: '#6ab4ff',
			 	    animation: {
			 	        duration: 0,
			 	        timingFunc: 'easeIn'
			 	    }
			 	})
			 }
			 if(option.enter==0){
			 	// 首页
				if(this.token){
					simulationBrushMenu({token:this.token}).then(res=>{
						console.log(res)
						this.data = res.data.data
						this.id = res.data.data[0].next[0].id
						simulationBrushList({token:this.token, id:this.id}).then(res=>{
							console.log(res)
							this.dataList = res.data.data
							this.pages = res.data.data.current_page
							this.allPages = res.data.data.last_page
						}).catch(err=>{
							console.log(err)
						})
					}).catch(err=>{
						console.log(err)
					})
				}else{
					simulationBrushMenu().then(res=>{
						console.log(res)
						this.data = res.data.data
						this.id = res.data.data[0].next[0].id
						simulationBrushList({id:this.id}).then(res=>{
							console.log(res)
							this.dataList = res.data.data
							this.pages = res.data.data.current_page
							this.allPages = res.data.data.last_page
						}).catch(err=>{
							console.log(err)
						})
					}).catch(err=>{
						console.log(err)
					})
				}
			 	
			 }else{
			 	console.log("复习入口")
			 	// 复习
			 	if(option.currents==0){
					 //错题
					 console.log("错题")
					 this.wrong = 1
					 uni.setNavigationBarTitle({
						title: "模拟刷题错题集"
					 });
					 simulationBrushWrong({token:this.token}).then(res=>{
						console.log(res)
						this.data = res.data.data
						this.id = res.data.data[0].next[0].id
						simulationBrushListWrong({token:this.token, id:this.id}).then(res=>{
							console.log(res)
							this.dataList = res.data.data
							this.pages = res.data.data.current_page
							this.allPages = res.data.data.last_page
						}).catch(err=>{
							console.log(err)
						})
					 }).catch(err=>{
						console.log(err)
					 })
			 	}else{
					 //收藏
					 console.log("收藏")
					 this.wrong = 2
					 uni.setNavigationBarTitle({
						title: "模拟刷题收藏集"
					 });
					 simulationBrushCollection({token:this.token}).then(res=>{
						console.log(res)
						this.data = res.data.data
						this.id = res.data.data[0].next[0].id
						simulationBrushListCollection({token:this.token, id:this.id}).then(res=>{
							console.log(res)
							this.dataList = res.data.data
							this.pages = res.data.data.current_page
							this.allPages = res.data.data.last_page
						}).catch(err=>{
							console.log(err)
						})
					 }).catch(err=>{
						console.log(err)
					 })
			 	}
			 }
		},
		onShow() {
			if(this.currents==0){
				//错题
				simulationBrushWrong({token:this.token}).then(res=>{
					console.log(res)
					this.data = res.data.data
					this.id = res.data.data[0].next[0].id
					simulationBrushListWrong({token:this.token, id:this.id}).then(res=>{
						console.log(res)
						this.dataList = res.data.data
						this.pages = res.data.data.current_page
						this.allPages = res.data.data.last_page
					}).catch(err=>{
						console.log(err)
					})
				}).catch(err=>{
					console.log(err)
				})
			}
		},
		methods: {
			changeTab(item,index) {
				this.check = index
				this.id = item.id
        if(this.enter==0){
        	// 首页
            if(this.token){
              simulationBrushList({token:this.token, id:this.id}).then(res=>{
                console.log(res)
                this.dataList = res.data.data
                this.pages = res.data.data.current_page
                this.allPages = res.data.data.last_page
              }).catch(err=>{
                console.log(err)
              })
            }else{
              simulationBrushList({id:this.id}).then(res=>{
                console.log(res)
                this.dataList = res.data.data
                this.pages = res.data.data.current_page
                this.allPages = res.data.data.last_page
              }).catch(err=>{
                console.log(err)
              })
            }
        }else{
        	console.log("复习入口")
        	// 复习
        	if(this.currents==0){
               //错题
              simulationBrushListWrong({token:this.token, id:this.id}).then(res=>{
                console.log(res)
                this.dataList = res.data.data
                this.pages = res.data.data.current_page
                this.allPages = res.data.data.last_page
              }).catch(err=>{
                console.log(err)
              })
        	}else{
               //收藏
              simulationBrushListCollection({token:this.token, id:this.id}).then(res=>{
                console.log(res)
                this.dataList = res.data.data
                this.pages = res.data.data.current_page
                this.allPages = res.data.data.last_page
              }).catch(err=>{
                console.log(err)
              })
        	}
        }
			},
			toDetiel(e) {
				question({token:this.token, unit_id:e.id}).then(res=>{
					console.log(res)
					if(res.data.code == -1 && res.data.data == '没有权限'){
						this.showAudition = true
					}else{
						if(e.is_complete==1 && this.wrong==0){
							//去报告
							uni.navigateTo({
								url: '/pages/index/summary?unitId='+e.id+'&title='+e.title+"&isTrue=0"
							})
						}else{
							//去题
							uni.navigateTo({
								url: '/pages/index/Details?unit_it='+e.id+'&wrong='+this.wrong+"&type="+1+"&title="+e.title+"&answerCard=1"+"&isTrue=0"
							})
						}
					}
				}).catch(err=>{
					console.log(err)
				})
			},
			// 关闭弹窗
			closeAudition() {
				this.showAudition = false
			},
			// 发送邀请码
			sendAuditionCode() {
				invitation({token:this.token, audition_code:this.auditionCode}).then(res=>{
					console.log(res)
					if(res.data.code==-1){
						this.$u.toast(res.data.data)
					}else{
						this.$u.toast(res.data.data)
						this.showAudition = false
					}
				}).catch(err=>{
					console.log(err)
				})
			}
		}
	}
</script>

<style lang="scss">
	.model{
		position: fixed;
		left: 0;
		top: 0;
		width: 100vw;
		height: 100vh;
		background-color: rgba(0, 0, 0, 0.4);
		z-index: 5;
		.models{
			width: 90%;
			left: 50%;
			right: 50%;
			position: fixed;
			margin-top: 65%;
			// border-radius: 20rpx;
			// padding: 10rpx 0;
			// background-color: #fff;
			transform: translate(-50%,-50%);
			text-align: center;
			img{
				width: 101%;
				height: 200rpx;
			}
			.contents{
				border-bottom-left-radius: 20rpx;
				border-bottom-right-radius: 20rpx;
				padding: 10rpx 0;
				margin: 0 30rpx;
				margin-top: -50rpx;
				background-color: #fff;
			}
			.closeModel{
				top: -20rpx;
				right: -10rpx;
				width: 50rpx;
				height: 50rpx;
				line-height: 50rpx;
				color: #fff;
				border: 1px solid #fff;
				font-size: 38rpx;
				text-align: center;
				position: absolute;
				border-radius: 50%;
			}
			.modelTitle{
				font-weight: 600;
				margin: 30rpx 0;
			}
			input{
				border:1rpx solid #d1d1d1;
				width: 90%;
				margin: 0 5%;
				line-height: 88rpx;
				height: 88rpx;
				border-radius: 10rpx;
			}
			.buttons{
				display: flex;
				justify-content: space-between;
				padding: 50rpx 20rpx;
				button{
					color: #fff;
					border-radius: 10rpx;
					width: 45%;
					line-height: 80rpx;
				}
				.sure{
					background-color: #e73928;
				}
				.contact{
					background-color: #c0c0c0;
				}
			}
		}
	}
	.times{
		overflow-x: scroll;
		white-space: nowrap;
		margin-bottom: 40rpx;
		height: 88rpx;
		view{
			display: inline-block;
			width: 48%;
			margin-right: 2%;
			border-radius: 10rpx;
			background-color: #fff;
			font-size: 32rpx;
			text-align: center;
			line-height: 88rpx;
		}
		.checked{
			color: #fff;
			background-color: #6ab4ff !important;
		}
	}
	.content{
		display: flex;
		justify-content: space-between;
		padding: 0 20rpx;
		.tabarList{
			flex: 3;
			margin-right: 50rpx;
			text-align: center;
			background-color: #fff;
			padding: 30rpx 20rpx;
			border-radius: 10rpx;
			height: 80vh;
			view{
				width: 180rpx;
				padding: 0 20rpx;
				border-radius: 100rpx;
				line-height: 68rpx;
				margin-bottom: 20rpx;
				overflow-x: scroll;
				overflow-y: hidden;
				white-space: nowrap;
				height: 68rpx;
			}
			.checkTabar{
				background-color: #6ab4ff;
				color: #fff;
			}
		}
		.questionList{
			flex: 8;
			background-color: #fff;
			padding: 30rpx 20rpx;
			height: 80vh;
			border-radius: 10rpx;
			.question{
				font-size: 28rpx;
				display: flex;
				justify-content: space-between;
				align-items: center;
				.title{
					width: 170rpx;
					line-height: 78rpx;
					white-space: nowrap;
					overflow-x: scroll;
				}
				.unfinished{
					background-color: #ffded1;
					font-size: 20rpx;
					line-height: 50rpx;
					height: 50rpx;
					margin: 20rpx 0;
					color: #c63904;
					width: 120rpx;
					text-align: center;
					border-radius: 50rpx;
				}
				.notDone{
					background-color: #f0f0f0;
					font-size: 20rpx;
					line-height: 50rpx;
					height: 50rpx;
					margin: 20rpx 0;
					color: #898989;
					width: 120rpx;
					text-align: center;
					border-radius: 50rpx;
				}
				.finished{
					background-color: #c3f6db;
					font-size: 20rpx;
					line-height: 50rpx;
					height: 50rpx;
					margin: 20rpx 0;
					color: #148d4e;
					width: 120rpx;
					text-align: center;
					border-radius: 50rpx;
				}
			}
		}
	}
	.body{
		padding: 20rpx;
		background-color: #f7f7fb;
		height: 100vh;
	}
	.bodyNight{
		padding: 20rpx;
		background-color: #3b3b3b;
		height: 100vh;
		.times{
			view{
				background-color: #303030;
				color: #fff;
			}
		}
		.content{
			.tabarList{
				background-color: #303030;
				color: #fff;
			}
			.questionList{
				background-color: #303030;
				color: #fff;
				.question{
					border-bottom: 1rpx solid #fff;
				}
			}
		}
	}
</style>